.reviews {
  padding: 0;
}
.reviews ul,
.reviews li {
  display: inline-block;
  margin: 0;
  padding: 0 0 16px;
  list-style: inherit;
}
ul.reviews {
  font-size: 12.8px;
  line-height: 18px;
  vertical-align: baseline;
  display: inline-block;
}
.user-name {
  display: inline-block;
  text-align: left;
  font-weight: 800;
}
.user-date {
  text-align: right;
  float: right;
}
.user-header {
  clear: both;
  background-color: #bdccdb;
}
.user-text {
  display: inline-block;
}
.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0;
  display: none;
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
}
.content--left {
  width: 49.2%;
}
@media (max-width: 1100px) {
  .content--left {
    width: 66.13%;
  }
}
@media (max-width: 830px) {
  .content--left {
    width: 100%;
  }
}
.content--right {
  width: 49.2%;
}
@media (max-width: 1440px) {
}
@media (max-width: 1100px) {
  .content--right {
    width: 32.26%;
  }
}
@media (max-width: 830px) {
  .content--right {
    width: 100%;
  }
}
