.section {
  clear: both;
  padding: 0px;
  margin: 0px;
}
.group {
  zoom: 1;
}
.col {
  display: block;
  float: left;
  margin: 1% 0 1% 1.6%;
  padding: 1em 0;
}
.col:first-child {
  margin-left: 0;
}
@media (max-width: 830px) {
  .col {
    margin: 0;
  }
}
body {
  margin: 0;
  padding: 1em 1em 0 1em;
}
@media (max-width: 360px) {
  body {
    padding: 0;
  }
}
.wrapper {
  max-width: 2048px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}
.float {
  float: left;
}
.header {
  padding: 1em 0;
}
.logo {
  width: 49.2%;
}
@media (max-width: 830px) {
  .logo {
    text-align: center;
    width: 100%;
  }
}
.logo img {
  max-height: 135px;
  width: auto;
  display: inline;
}
@media (max-width: 830px) {
  .logo img {
    max-height: 96px;
  }
}
.callmenow-small {
  position: absolute;
  top: 0;
  right: 0;
  height: 3em;
}
@media (min-width: 830px) {
  .callmenow-small {
    display: none!important;
  }
}
.callmenow-small span {
  height: 3em;
  margin: 0 2em;
}
