/*  GRID OF FOUR   ============================================================================= */

.span_4 {
  width: 100%;
}

.span_3 {
  width: 74.6%;
}

.span_2 {
  width: 49.2%;
}

.span_1 {
  width: 23.8%;
}




/*  GO FULL WIDTH AT LESS THAN 480 PIXELS */

@media only screen and (max-width: 480px) {
  .span_4 {
    width: 100%;
  }
  .span_3 {
    width: 100%;
  }
  .span_2 {
    width: 100%;
  }
  .span_1 {
    width: 100%;
  }
}
