@media (max-width: 830px) {
  .menu {
    display: none;
  }
}
.menu > li.active {
  background-color: #fae1d0;
}
#nav {
  position: relative;
  text-align: center;
  min-height: 3em;
  background: #bdccdb;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  float: left;
  width: 100%;
  z-index: 9;
}
#nav ul,
#nav li {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
.callmenow {
  text-align: right;
  margin-top: 1em;
  float: right;
}
@media (max-width: 830px) {
  .callmenow {
    display: none;
  }
}
.callmenow ul,
.callmenow li {
  display: inline;
  margin: 0;
  padding: 0;
}
.callmenow a {
  display: block;
  padding: 13px 24px;
  padding: .6em 0em;
  margin-left: 8px;
  text-decoration: none;
  color: #555555;
}
.callmenow a:hover {
  color: #fe6600;
}
#burger-check:checked ~ .menu {
  display: block!important;
  color: #fe6600;
  padding-top: 3em;
}
#burger-check:checked ~ .menu li {
  display: block;
  float: left;
  width: 33%;
}
@media (max-width: 500px) {
  #burger-check:checked ~ .menu li {
    width: 50%;
  }
}
#burger-check:checked ~ .callmenow-small {
  display: none;
}
.menu {
  display: inline-block;
}
.menu a {
  transition: background-color 0.5s;
  display: block;
  padding: 13px 24px;
  color: #555555;
  text-decoration: none;
  font-size: 1em;
  font-family: 'Oswald';
  font-weight: 300;
}
.menu a.active {
  background-color: #fae1d0;
}
.menu a:hover {
  background-color: #fe6600;
}
.menu > li {
  display: inline-block;
}
@media (max-width: 830px) {
  .menu {
    display: none;
  }
}
#burger-check {
  display: none;
}
label.hamburger-button {
  display: none;
  background-image: url('data:image/svg+xml;charset=UTF-8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="32" viewBox="0 0 24 32"><path d="M0 6v4h24V6H0zm0 12h24v-4H0v4zm0 8h24v-4H0v4z"></path></svg>');
  background-repeat: no-repeat;
  background-position: center;
  background-color: #feca00;
  width: 3em;
  height: 3em;
  cursor: pointer;
  position: absolute;
  left: 0;
  top: 0;
}
@media (max-width: 830px) {
  label.hamburger-button {
    display: block;
  }
}
#burger-check:checked ~ label {
  background-color: #99ca00;
}
#burger-check:checked + .callmenow-small {
  display: none;
}
@media (max-width: 480px) {
  .callmenow-small span {
    margin: 0 1em!important;
  }
}
.callmenow-small li {
  display: inline-block;
}
